.profile {
  width: 300px;
  background-color: blanchedalmond;
  margin: 0 auto;
  padding: 10px;
}

.description {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.description__img {
  width: 150px;
  border: 2px solid black;
  padding: 5px;
  border-radius: 50%;
}

.description__name {
  font-size: 24px;
  font-weight: bold;
}

.stats {
  list-style: none;
  display: flex;
}

.quantity {
  font-weight: bold;
}
