.item {
  width: 500px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 100px;
  border-radius: 5px;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}
.status {
  width: 20px;
  height: 20px;
  border-radius: 50%;
}
.avatar {
  width: 48px;
}
.name {
  color: teal;
}
