.table {
  margin: 0 auto;
  border-collapse: collapse;
  border: 2px solid blue;
  table-layout: fixed;
  width: 100%;
}
.headtr {
  background-color: rgb(202, 199, 247);
}
.th,
.td {
  padding: 20px;
  border: 1px solid black;
}

.bodytr:nth-child(odd) {
  background-color: rgb(207, 206, 203);
}
.bodytr:nth-child(even) {
  background-color: rgb(185, 185, 185);
}
.td {
  text-align: center;
}
